import React from 'react';
import { Header } from './components';
import { Home, Cart, Contacts, Product, Order, NotFound } from './pages/';
import { Route, withRouter, Switch } from 'react-router-dom';

const App = withRouter((props) => {
  return (
    <div className="wrapper">
      <Header />
      <Switch>
        <Route path="/stroisektor-shop/" component={Home} exact />
        <Route path="/stroisektor-shop/product/:productId?" component={Product} />
        <Route path="/stroisektor-shop/cart" component={Cart} exact />
        <Route path="/stroisektor-shop/order" component={Order} exact />
        <Route path="/stroisektor-shop/contacts" component={Contacts} exact />
        <Route path="/stroisektor-shop/*" component={NotFound} />
      </Switch>
    </div>
  );
});

export default App;
